'use strict';

/**
* Test if a value is iterable-like.
*
* @module @stdlib/assert-is-iterable-like
*
* @example
* import isIterableLike from '@stdlib/assert-is-iterable-like';
*
* var bool = isIterableLike( [] );
* // returns <boolean>
*
* bool = isIterableLike( {} );
* // returns false
*/

// MODULES //

import hasIteratorSymbolSupport from '@stdlib/assert-has-iterator-symbol-support';
import constantFunction from '@stdlib/utils-constant-function';
import main from './main.js';


// MAIN //

var isIterable;
if ( hasIteratorSymbolSupport() ) {
	isIterable = main;
} else {
	isIterable = constantFunction( false );
}


// EXPORTS //

export default isIterable;
